.section__form {
    margin-top: 35px;
}

.form {
    display: flex;
    flex-direction: column;
    max-width: 600px;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 35px;
    padding: 20px;

    border: 2px solid black;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 1),
        -23px 0 20px -23px rgba(0, 0, 0, 0.8),
        23px 0 20px -23px rgba(0, 0, 0, 0.8), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}

.label {
    display: flex;
    flex-direction: column;
    font-size: 22px;
    font-weight: 700;
    margin-bottom: 30px;
}

.inputName {
    max-width: 250px;
    margin-top: 15px;
    padding: 5px 10px;
}

.btn {
    display: block;
    max-width: 120px;
    font-size: 16px;
    padding: 5px;
    cursor: pointer;
    border: none;
    border-radius: 5%;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 1),
        -23px 0 20px -23px rgba(0, 0, 0, 0.8),
        23px 0 20px -23px rgba(0, 0, 0, 0.8), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}

.btn:active {
    background-color: rgba(8, 51, 172, 0.719);
    color: #fff;
}
